import React  from 'react'
import styled, {css} from 'styled-components';
import {backgroundPulse} from 'components/icons/keyframes'
import size                 from 'components/layout/responsive'


// #a9a9a9
const Checkbox = ({ className, checked, ...props }) => (
    <MainContainer>
        <CheckboxContainer className={className}>
            <HiddenCheckbox checked={checked} {...props} />
            <StyledCheckbox checked={checked} {...props}>
                <Icon viewBox="0 0 24 24">
                    <circle cx="12" cy="12" r="11" fill="#979797" />
                </Icon>
            </StyledCheckbox>
        </CheckboxContainer>
        {props.label ? <Label checked={checked} {...props}>{props.label}</Label> : "" }
    </MainContainer>
)


const MainContainer = styled.label`
    display:flex;
    align-items:center;
`

const HiddenCheckbox = styled.input.attrs({ type: 'checkbox' })`
    // Hide checkbox visually but remain accessible to screen readers.
    // Source: https://polished.js.org/docs/#hidevisually
    border: 0;
    clip: rect(0 0 0 0);
    clippath: inset(50%);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    white-space: nowrap;
    width: 1px;
    &:focus {
        outline:0;
    }
`


const Icon = styled.svg`
    display:flex;
    fill: none;
    stroke:#a9a9a9;
`


const StyledCheckbox = styled.div`
    display: inline-block;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    border: 2px solid white;
    transition: all 150ms;
    ${HiddenCheckbox}:focus + & {
        box-shadow: 0 0 0 3px rgba(69, 115, 205, 0.1);
    }
    ${Icon} > circle {
        /* visibility: ${props => props.checked ? 'visible' : 'hidden'} */
        fill: ${props => props.checked ? '#e90202' : '#979797'};
        
        ${props => props.disabled && css`
            -webkit-animation: ${backgroundPulse} 1s infinite linear;
            animation: ${backgroundPulse} 1s infinite linear;
        `}

    }

`

const CheckboxContainer = styled.div`
    /* display: inline-block; */
    display:flex;
    margin-right:5px;
`

const Label = styled.span`
    font-size: 15px;
    font-weight: 400;
    color:#a9a9a9;
    ${props => props.checked && !props.disabled && css`
        color:black;
    `}

    ${props => (!props.disabled && !props.checked) && css`
        cursor:pointer;
        &:hover {
            color: black;
        }
    `}

    ${_ => size.tablet(`
        font-size:14px;
        line-height:1.43;
    `)}

    ${_ => size.mobile(`
        font-size:12px;
    `)}


`
export default Checkbox
