import { createSelector } from 'reselect'

export const LOGIN_ON_REFRESH = 'LOGIN_ON_REFRESH'

const selectArticle = state => state.app;

export const articleSelector = createSelector(
    [selectArticle],
    (app) => app.articleId
);


const INITIAL_STATE = {
    loggedIn: false,
    admin: false,
    articleId: null,
    env: window.env
}

const appReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case LOGIN_ON_REFRESH: {
            console.log('loggin in on app reducer');
            console.log(action);

            const newState = {
                ...state,
                env         : action.env,
                admin       : action.admin,
                articleId   : action.articleId,
                loggedIn    : action.loggedIn,
            }

            return newState;
        }

        default: 
            return state
    }
}

const appSelectors = {
    articleSelector
};

const actionTypes = {
    LOGIN_ON_REFRESH
}
export {
    appSelectors,
    actionTypes
}


export default appReducer;