import { createSelector } from 'reselect'


const selectComments = state => state.comments;


export const selectStatusPending = createSelector(
    [selectComments],
    (comments) => comments.statusPending
);
export const selectTotalRootLoaded = createSelector(
    [selectComments],
    (comments) => comments.totalRootLoaded
);

export const loadMore = createSelector(
    [selectComments],
    (comments) => {
        return comments.totalRootCount > comments.totalRootLoaded;
    }
);


export const selectPosted = createSelector(
    [selectComments],
    (comments) => comments.posted
);

export const selectRootCount = createSelector(
    [selectComments],
    (comments) => comments.totalRoot
);

export const selectRefresh = createSelector(
    [selectComments],
    (comments) => comments.refresh
);

export const selectThreadLevel = createSelector(
    [selectComments],
    (comments) => comments.threadLevel
);

export const selectComment = createSelector(
    [selectComments],
    (comments) => comments.comments
);

export const selectAllComments = createSelector(
    [selectComments],
    (comments) => comments.comments
);

export const selectStatusView = createSelector(
    [selectComments],
    (comments) => {
        return comments.status
    }
);
export const selectThreaded = createSelector(
    [selectComments],
    (comments) => {
        return comments.threaded
    }
);
export const selectLimit = createSelector(
    [selectComments],
    (comments) => {
        return comments.limit
    }
);


export const selectCommentCount = createSelector(
    [selectComments],
    (comments) => {
        return comments.total
    }
);

export const selectLoadedRootCount = createSelector(
    [selectAllComments],
    (comments) => comments.length
);




export const selectPublishedComments = createSelector(
    [selectComments],
    (comments) => {
        return comments.filter(c => c.status === 'published')
    }
);

export const selectDraftComments = createSelector(
    [selectComments],
    (comments) => {
        return comments.filter(c => c.status === 'draft')
    }
);

export const selectPublishedCommentCount = createSelector(
    [selectPublishedComments],
    (comments) => {
        return typeof comments !== 'undefined' ? comments.length : 0
    }
);

export const selectDraftCommentCount = createSelector(
    [selectDraftComments],
    (comments) => {
        return typeof comments !== 'undefined' ? comments.length : 0
    }
);



export const activeReplies = createSelector(
    [selectComments],
    (comments) => comments.active_replies
);
