import React                from 'react'
import ReactDOM             from 'react-dom'
import axios                from 'axios'

import {Provider}           from 'react-redux'
import {BrowserRouter}      from 'react-router-dom'
import {actionTypes}        from 'store/app/app'
import {userCreators}       from 'store/ducks/user'

// store
import store                from 'store/store'

// components
import App                  from './App'

// styles
import './index.css'



axios.defaults.baseURL = window.location.origin;
axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest';
axios.defaults.headers.common['X-CSRF-Token']     = window.csrfToken;
axios.defaults.headers.post['Content-Type']       = 'application/x-www-form-urlencoded';
axios.defaults.headers.get['Content-Type']        = 'application/x-www-form-urlencoded';
delete window.csrfToken;


let loggedIn = false;
let admin = true;
if (window._appJsConfig) {
    loggedIn = (true === (window._appJsConfig.isUserLoggedIn === 1))
    admin   =  (true === (window._appJsConfig.userHasBlogAccess === 1))
}
store.dispatch({
    type: actionTypes.LOGIN_ON_REFRESH, 
    loggedIn, 
    admin,
    articleId: window.articleId || null,
    env: window.env
});

store.dispatch(userCreators.fetchSelf());






