import React from 'react';
import ReactDOM from 'react-dom'
import styled, {css} from 'styled-components';

const modal = (props) => {

    let header = true;
    if (typeof props.header !== 'undefined' && props.header === false) {
        header = false;
    }
    
    return (
        ReactDOM.createPortal(
            <Modal id={props.name} data-dom="Top"> 
                <Window id="dialog" {...props}  data-dom="window"> 
                    <Container  data-dom="container"> 
                        {header && <Header  data-dom="header"> 
                            <Title>{props.title}</Title> 
                            <Close onClick={props.closeHandler}></Close> 
                        </Header>
                        } 
                        <ContentWindow id="dialogContent"  data-dom="contentWindow">
                            { props.children }
                        </ContentWindow> 
                    </Container> 
                </Window> 
            </Modal>, 
            document.getElementById('modal')
        )


    )
}


const Modal = styled.div`
    display:flex;
    position:fixed;
    top:0;
    left:0;
    width:100%;
    height:100%;
    z-index:1001;
    /* background: rgba(0,0,0,0.7); */
    /* background: rgba(135,224,253,0.7); */
/* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#77c6dd+0,87e0fd+100&0.7+0,0.7+100 */
background: -moz-radial-gradient(center, ellipse cover,  rgba(119,198,221,0.7) 0%, rgba(135,224,253,0.7) 100%); /* FF3.6-15 */
background: -webkit-radial-gradient(center, ellipse cover,  rgba(119,198,221,0.7) 0%,rgba(135,224,253,0.7) 100%); /* Chrome10-25,Safari5.1-6 */
background: radial-gradient(ellipse at center,rgb(130 169 105 / 70%) 0%,rgb(45 31 76 / 70%) 100%);
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#b377c6dd', endColorstr='#b387e0fd',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
`

const Window = styled.div`
    margin: auto auto;
    width:880px;
    max-height: 80%;
    /* min-height:300px; */
    background:white;
    padding:20px 35px 30px 35px;
    border-radius:3px;
    overflow:auto;
    -webkit-box-shadow: 1px 5px 11px 5px rgba(0,0,0,0.28); 
    box-shadow: 1px 5px 11px 5px rgba(0,0,0,0.28);
    /* tablet */
    @media screen and (min-width : 768px) and (max-width : 991px) {
        max-width:80%;
        height:auto;
    }

    /* mobile */
    @media screen and (max-width :767px) {
        width:auto;
        max-width:90%;
        padding:10px 18px 20px 18px;

    }

    ${props => props.width && css`
        width: ${props.width}
    `}
    ${props => props.height && css`
        height: ${props.height}
    `}
`

const Container= styled.div`
    position:relative;
    margin:auto;
    height:100%;
`

const Header = styled.div`
    min-height:30px;
    margin:0;
    overflow:auto;
`

const Title = styled.div`
    font-size: 18px;
    text-align:center;
    font-weight: 700;
    margin-bottom:20px;
    position:absolute;
`
const ContentWindow = styled.div`
    height:100%;
`

const Close = styled.div`
    display:block;
    cursor:pointer;
    position:absolute;
    top:0;
    right:0;
    z-index:99999;
    width: 20px;
    height: 20px;

    &:before, &:after {
        content:'';
        position:absolute;
        width:100%;
        height:1px;
        background-color:#595859;
        top:50%;
    }
    &:before {
        -webkit-transform:rotate(45deg);
        -moz-transform:rotate(45deg);
        transform:rotate(45deg);
    
    }
    &:after {
        -webkit-transform:rotate(-45deg);
        -moz-transform:rotate(-45deg);
        transform:rotate(-45deg);
    }

    
`

// ${props => props.borderTop && css`
// border-top: 1px solid #595859;
// padding-top: 10px;
// margin-top:33px;
// `}

export default modal;
