import * as selectors   from 'store/messages/selectors'

const SET_MESSAGE= "SET_MESSAGE";
const HIDE_MESSAGE = "HIDE_MESSAGE";

const setMessage = (message) => {
    return {
        type: SET_MESSAGE,
        message: message
    }
}
const hideMessage = () => {
    return {
        type: HIDE_MESSAGE
    }
}

const initState = {
    message: null,
    isOpen: false,
};

const messageReducer = (state = initState, action) => {
    const { message } = action;

    if (message) {
        return {
            message,
            isOpen: true
        }
    } else if (action.type === HIDE_MESSAGE) {
        return {
            message: null,
            isOpen: false
        }
    }

    return state;
}




const messageActions = {
    setMessage,
    hideMessage
};

const messageTypes = {
    SET_MESSAGE,
    HIDE_MESSAGE
}



export {
    messageTypes, 
    messageActions,
    selectors
};

export default messageReducer;