const isset = (p) => {
    if (typeof p !== 'undefined') {
        return true;
    }
    return false;
}
const fetchComments = (commentId = null, path = [], params = {}) => {
    console.log(params);
    const state = store.getState();
    const sort = state.comments.sort || 'created_at';
    const limit = state.comments.limit || false;
    const status = state.comments.status || '';
    const toppicks = isset(params['toppicks']) && params['toppicks'] === true;
    const threaded = state.comments.threaded;
    const sortdirection = state.comments.sortDirection || 'asc';
    const more = isset(params['more']) && params['more'] === true;

    const reqParams = {sort,sortdirection,status,threaded,limit};
    let collection = 'article';
    if (commentId === null) {
        reqParams['articleid'] = state.app.articleId;
        if (toppicks) {
            reqParams['toppicks'] = true;
        }
    } else {
        collection = 'replies';
        reqParams['commentid'] = commentId;
    }
    if (isset(params['offset']) && params['offset'] > 0) {
        reqParams['offset'] = params['offset'];
    }
    if (isset(params['depth']) && params['depth'] > 0) {
        reqParams['depth'] = params['depth'];
    }
    if (more) {
        reqParams['offset'] = state.comments.totalRootLoaded;
        reqParams['root'] = true;
    }

    if (state.comments.threadLevel > 0 && threaded) {
        reqParams['flattenlevel'] = state.comments.threadLevel;
    }
    
    // if (process.env.NODE_ENV === 'development') {
    //     return dispatch => {
    //         dispatch({
    //             type: REFRESHING,
    //             payload: true
    //         });
    
    //         dispatch({type:'void'});

    //         dispatch({
    //             type: REFRESHING,
    //             payload: false
    //         });

    //     }
    // }

    console.log(reqParams);

    return dispatch => {
        
        dispatch({
            type: REFRESHING,
            payload: true
        });


        return axios.get('/api/comment?' + qs.stringify(reqParams)).then(r => {
            // console.log(r);
            if (r.data.success === 1) {

                console.log(collection);


                if (collection === 'article') {
                    const totalLoadedRootCount = r.data.root_comments_count ? parseInt(r.data.root_comments_count): 0;
                    const totalRootCount = r.data.total_root_count ? parseInt(r.data.total_root_count): 0;
                    const totalPicks = r.data.pick_count ? parseInt(r.data.pick_count): 0;
                    const total = r.data.total_count ? parseInt(r.data.total_count): 0;
                    const stateUpdate = {
                        type: LOAD_COMMENTS,
                        total: total,
                        append: more ? true : false,
                        comments: r.data.comments,
                        totalRootCount: totalRootCount,
                        totalPicksLoaded: totalPicks,
                        totalLoadedRootCount
                    };
                    dispatch(stateUpdate);
                } else {
                    dispatch({
                        type: ADD_REPLIES,
                        path,
                        comments: r.data.comments,
                        count: r.data.count
                    });

                }

                dispatch({
                    type: REFRESHING,
                    payload: false
                });
        
            }
        }).catch(e => {
            dispatch({
                type: REFRESHING,
                payload: false
            });

            // dispatch({
            //     type: POST_COMMENT,
            //     payload: comment,
            //     // admin: true,
            // });
        });
    }
}

