const postComment = (comment) => {
    const state = store.getState();
    comment['articleId'] = state.app.articleId;
    console.log(comment);
    
    return dispatch => {
    
        dispatch({
            type: POSTING,
            payload: "root"
        });

        return axios.post('/api/comment/post-comment', comment).then((r) => {

            console.log(r);
            if (r.data.success === 1) {

                const newComment = r.data.comment;
                newComment.comments['path'] = comment['path'];

                dispatch({
                    type: POST_COMMENT,
                    payload: newComment,
                });
            }
        }).catch(e => {

            
            // dispatch({
            //     type: POST_COMMENT,
            //     payload: comment,
            //     // admin: true,
            // });

        });
    }
}

const editComment = (id, comment) => {

    const params = {
        comment: comment['comment'],
        comment_id: id
    };

    return dispatch => {

        return axios.post('/api/comment/edit-comment', params).then((r) => {

            console.log(r);
            if (r.data.success === 1) {

                dispatch({
                    type: EDIT_COMMENT,
                    comment: r.data.comment,
                    path: comment['path']
                });

            } else if (r.data.error) {
                dispatch({
                    type: errorTypes.SET_ERROR,
                    error: r.data.error,
                });
            }


        }).catch(e => {
            console.log('in the catch');
            comment['comment'] = "didn't save in database";
            console.log(e);
            dispatch({
                type: errorTypes.SET_ERROR,
                error: "didn't save in database",
            });

        });
    }
}



const updateStatus = (id, path, status) => {
    
    const params = {
        status,
        comment_id: id
    };
    
    return dispatch => {

        dispatch({
            type: STATUS_PENDING,
        });

        


        return axios.post('/api/comment/comment-status', params).then((r) => {
            console.log(r);
            if (r.data.success === 1) {
                dispatch({
                    type: COMMENT_STATUS,
                    status,
                    path: path
                });

            } else if (r.data.error) {
                dispatch({
                    type: errorTypes.SET_ERROR,
                    error: r.data.error,
                });
            }

            dispatch({
                type: STATUS_PENDING,
            });
    
        }).catch(e => {
            console.log('in the catch');
            console.log(e);
            dispatch({
                type: errorTypes.SET_ERROR,
                error: `didn't save ${status} in database`,
            });
            dispatch({
                type: STATUS_PENDING,
            });
    
        });
    }
}

