import React from 'react'
import styled, {css}        from 'styled-components'

import Checkbox             from 'components/Form/checkbox'
import Bin                  from 'components/icons/bin'
import size                 from 'components/layout/responsive'

const admin = (props) => {

    const {
        status, 
        statusHandler, 
        deleteHandler, 
        topPickHandler, 
        topPick, 
        editHandler, 
        editActive,
        pending
    } = props;

    return (
        <Container>

            <StatusBar>
                <Checkbox disabled={pending} label="Published" checked={status === 'published'   ? true : false} name="published"   onChange={() => statusHandler('published')} />
                <Checkbox disabled={pending} label="Draft"     checked={status === 'draft'       ? true : false} name="draft"       onChange={() => statusHandler('draft')} />
                <Checkbox disabled={pending} label="Declined"  checked={status === 'disapproved' ? true : false} name="disapproved" onChange={() => statusHandler('disapproved')} />
            </StatusBar>

            <SecondRow>
                <Middle>
                    <Checkbox label="Top comment" checked={topPick} name="top_pick"   onChange={topPickHandler} />
                </Middle>
                <BinContainer>
                    <Edit active={editActive} onClick={editHandler} />
                    <Bin handler={deleteHandler} status={status === 'deleted' ? true : false} />
                </BinContainer>
            </SecondRow>

        </Container>
    )
}

const Container = styled.div`
    display:flex;
    height:33px;
    background: rgba(216, 216, 216, 0.29);
    margin-bottom: 10px;
    padding:7px 15px;
    margin-top:40px;

    ${_ => size.tablet(`
        padding:7px 10px;
    `)}

    ${_ => size.mobile(`
        flex-direction: column;
        height:78px;
        padding:7px;
        margin-top:20px;
    `)}

`
const StatusBar = styled.div`
    display:flex;
    align-items:center;
    justify-content:space-between;
    flex-basis: 45%;
    padding-right:44px;
    border-right: solid 1px #979797;

    ${_ => size.tablet(`
        flex-basis: 49%;
        padding-right:20px;
    `)}
    ${_ => size.mobile(`
        border-right: none;
        border-bottom: solid 1px #979797;
        align-items:center;
        max-height:50%;
        flex-basis: 100%;
        padding-right:0;
        padding-bottom: 5px;
    `)}
`

const SecondRow = styled.div`
    display:flex;
    align-items:center;
    flex-grow: 1;
    height:50%;
    padding-top:7px;
`


const BinContainer = styled.div`
    display:flex;
    margin-left:auto;
    padding-right: 14px;
    
    ${_ => size.tablet(`
        padding-right: 0;
    `)}

    ${_ => size.mobile(`
        padding-right: 0;
    `)}


`

const Middle = styled.div`
    display:flex;
    align-items:center;
    padding-left:40px;

    ${_ => size.tablet(`
        padding-left:20px;
    `)}
    ${_ => size.mobile(`
        padding-left:0;
    `)}
    /* align-items:center; */

`

const Edit = styled.button`
    cursor:pointer;
    margin-right:40px;
    height:22px;
    width:19px;
    border:none;
    background: url('data:image/svg+xml;base64,');
    ${props => props.active && css`
        background: url('data:image/svg+xml;base64,');
    `}
        /* background: url('data:image/svg+xml;uft8,<svg xmlns="http://www.w3.org/2000/svg" width="19" height="22" viewBox="0 0 19 22"><g fill="none" fill-rule="evenodd"><path fill="#A9A9A9" fill-rule="nonzero" d="M14.226.937c-.93-.434-2.038-.032-2.473.9l-.196.421 3.372 1.573.197-.421c.434-.931.031-2.039-.9-2.473zm-3.259 2.586L5.66 14.904l.114 4.158 3.259-2.585L14.34 5.096l-3.373-1.573z" transform="translate(0 -.5)"/><path stroke="#A9A9A9" stroke-linecap="square" d="M0.5 21.5L18.5 21.5" transform="translate(0 -.5)"/></g></svg>'); */
    &:focus {
        outline:0;
    }

    ${_ => size.tablet(`
        margin-right:20px;
    `)}


`

export default admin;