import { createSelector }   from 'reselect'
import axios                from 'axios'
import {errorTypes}         from 'store/errors/error'


const LOGIN_USER_ON_REFRESH = 'user/LOGIN_ON_REFRESH'
const LOGIN_ON_REFRESH = 'LOGIN_ON_REFRESH'

const UPDATE_USER_NAME = 'user/UPDATE_USER_NAME'

const userTypes = {
    LOGIN_USER_ON_REFRESH,
    UPDATE_USER_NAME
};

const isset = (p) => {
    if (typeof p !== 'undefined') {
        return true;
    }
    return false;
}


const fetchSelf = () => {
    console.log('FETCHING SELF');

    if (process.env.NODE_ENV === 'development') {
        return dispatch => dispatch({type:'void'});
    }

    return dispatch => {
        console.log('dispatching top pick');
        
        return axios.get('/api/user/self').then((r) => {

            console.log(r);
            if (r.data.success === 1) {

                dispatch({
                    type: LOGIN_USER_ON_REFRESH,
                    user: r.data.self,
                });

            } else if (r.data.error) {
                dispatch({
                    type: errorTypes.SET_ERROR,
                    error: r.data.error,
                });
            }
        }).catch(e => {
            console.log('in the catch');
            console.log(e);
            console.log(e.message);
            dispatch({
                type: errorTypes.SET_ERROR,
                error: e.message,
            });

        });
    }
}




let INITIAL_STATE = {
    admin: false,
    isLoggedIn: false,
    commenter: false,
}

if (process.env.NODE_ENV === 'development') {
    INITIAL_STATE = {
        admin: true,
        commenter: true,
        isLoggedIn: true,
        details: {
            user_id: 2,
            firstname: "Farah",
            position: "Managing Editor",
            company: "Monstors Inc",
            photo: "https://d3pbdxdl8c65wb.cloudfront.net/themes/ROchisDFz6yCY5/static/headshots/hancock@2x.jpg",
        }
    }
}

const userReducer = (state = INITIAL_STATE, action) => {

    
    switch (action.type) {

        case LOGIN_ON_REFRESH: {

            const newState = {
                ...state,
                admin       : action.admin,
                loggedIn    : action.loggedIn,
            }

            return newState;
        }

        case LOGIN_USER_ON_REFRESH: {
            const user = action.user;
            if (user === 'Guest') {
                return {
                    ...state,
                    isLoggedIn  : false,
                    admin       : false,
                };
            }
            const details = {
                photo: user.profileMedia.path,
                user_id: parseInt(user.id),
                company: user.business_name,
                // position: user.additionalInfo.job_position,
                lastname: user.lastname,
                firstname: user.firstname,
            };

            let commenter = false;
            if (isset(user.additionalInfo) && isset(user.additionalInfo.allow_comments) && user.additionalInfo.allow_comments === "true") {
                commenter = true;
            }

            const newState = {
                ...state,
                isLoggedIn  : user.id ? true : false,
                // admin       : true,
                commenter   : commenter,
                details     : {
                    ...details
                }
            }
            console.log(newState);
            return newState;
        }
    
        case UPDATE_USER_NAME: {

            const newState = {
                ...state,
                details: {
                    ...state.details,
                    name: action.name
                }
            }
            return newState;
        }


        default:
            return state;
    }
}





const selectUser = state => state.user;
export const selectCurrentUser = createSelector(
    [selectUser],
    user => user
);




const userCreators = {
    fetchSelf,
};

const userSelectors = {
    selectUser
};

export {
    userCreators,
    userTypes,
    userSelectors
};

export default userReducer;