import React, { Component }                 from 'react'
import styled, {css}                        from 'styled-components'
import { connect }                          from 'react-redux'
// import moment                               from 'moment'
import format                               from 'date-fns/format'
import parse                                from 'date-fns/parse'
import formatDistanceToNow                  from 'date-fns/formatDistanceToNow'
import differenceInHours                    from 'date-fns/differenceInHours'
import { createStructuredSelector }         from 'reselect'

// Store
import { commentCreators, commentTypes, selectors }    from 'store/comments/comments'
import { userSelectors }                    from 'store/ducks/user'

// Components
import CommentForm          from 'components/commentForm/commentForm'
import CommentCount         from 'components/icons/speechbubble/speechbubble'
import Admin                from 'components/comments/admin'
import RippleSpinner        from 'components/spinners/RippleSpinner'
import Modal                from 'components/modals/modal'
import size                 from 'components/layout/responsive'

class Comment extends Component {

    constructor() {
        super();
        this.formInput = React.createRef();

        this.state = {
            enable_reply: false,
            enable_edit: false,
            show_original: false,
            edit_submit: false,
            deleteModal: false,
            updateStatus: "",
        }
    }


    
    enableReply = () => {
        const reply = !this.state.enable_reply;
        this.setState({enable_reply: reply, enable_edit: false}, () => {
            if (reply === true) {
                this.formInput.current.focus();
            }
        });
    }
    enableEdit = () => {
        const edit = !this.state.enable_edit;
        this.setState({enable_edit: edit, enable_reply:false}, () => {
            if (edit === true) {
                // this.formInput.current.focus();
            }
        });
    }

    toggleDeleteModal = (status = "") => {
        this.setState(prevState => ({deleteModal: !prevState.deleteModal, updateStatus: status}));
    }

    updateStatus = (status) => {
        if (status === this.props.status) return;
        if (status === 'deleted' || status === 'disapproved') {
            this.setState({deleteModal: false, updateStatus:""});
        }
        this.props.updateStatus(this.props.id, this.props.path, status);
    }

    editComment = (comment) => {
        this.setState({editSubmit: true});
        return this.props.editComment(this.props.id, comment).then( (r) => {
            console.log(' in the THEN of comment.js');
            console.log(r)
            this.setState({editSubmit: false});
        });
    }
    
    topPick = () => {
        this.props.topPick(this.props.id, this.props.path);
    }
    showOriginal = () => {
        this.setState({show_original: true}, () => {
            if (!this.props.previous) {
                this.props.getPrevious(this.props.id, this.props.path);
            }
        });
    }
    hideOriginal= () => {
        this.setState({show_original: false});
    }


    hideReplies = () => {
        return this.props.showReplies(this.props.path);
    }


    showReplies = () => {

        if (this.props.replies.length > 0) {
            return this.props.showReplies(this.props.path);
        }
        this.loadMore();
    }

    loadMore = (depth = 1) => {
        this.props.toggleLoading(this.props.path);
        const params= {};
        if (this.props.replies.length > 0) {
            if (depth !== false) {
                params['depth'] = depth;
            }
            params['offset'] = this.props.replies.length;
        };
        this.props.fetchReplies(this.props.id, this.props.path, params);
    }

    render() {

        if (this.state.deleteModal) {
            return (
                <Modal closeHandler={this.toggleDeleteModal}>
                    <div style={{width:"90%", margin: "0 auto"}}>
                        <p>Are you sure you want want to {this.state.updateStatus} this comment?</p>
                        <Buttons style={{marginTop:"30px"}}>
                            <Button bold={true} red={true} onClick={() => this.updateStatus(this.state.updateStatus)}>Yes</Button>
                            <Button onClick={this.toggleDeleteModal}>No</Button>
                        </Buttons>
                    </div>
                </Modal>
            )
        }


        const hasReplies       = this.props.reply_count > 0;
        const hasLoadedReplies = this.props.replies.length > 0;
        const repliesShowing   = this.props.renderReplies === true;
        const stopIndent       = this.props.path.length > this.props.threadLevel;
        const flattening       = this.props.path.length >= this.props.threadLevel;
        
        let show_reply_label = hasReplies && !repliesShowing && !stopIndent;
        const show_hide_reply_label = (hasReplies && repliesShowing && hasLoadedReplies);





        // const published = moment(this.props.publishedDateTime, 'YYYY-MM-DD hh:mm:ss');
        // let publishedDate = published.format("D MMM YYYY");

        const published = parse(this.props.publishedDateTime, "yyyy-MM-dd HH:mm:ss", new Date());
        let publishedDate = format(published, "d MMMM yyyy");
        if (differenceInHours(new Date(), published) < 12) {
            publishedDate = formatDistanceToNow(published, {addSuffix: true});
        };
        // console.log( formatDistanceToNow(published, {addSuffix: true}) );

        let edited = false;

        if (this.props.updatedCommentDateTime) {
            const editedDate = parse(this.props.updatedCommentDateTime, "yyyy-MM-dd HH:mm:ss", new Date());
            edited = format(editedDate, "d MMMM yyyy");
            if (differenceInHours(new Date(), editedDate) < 12) {
                edited = formatDistanceToNow(editedDate, {addSuffix: true});
            };
    
        }

        let reply_to = "&nbsp;";
        if (this.props.parent_id && flattening) {
            reply_to = "Replying to " + this.props.reply_to;
        } 

        let organisation = [];
        if (this.props.position) {
            organisation.push(this.props.position);
        }
        if (this.props.organisation) {
            organisation.push(this.props.organisation);
        }

        const organisationString = organisation.join(', ');

        return (
            <OuterCommentContainer last={this.props.last} id={this.props.id} key={this.props.id} repliesShowing={repliesShowing} child={this.props.child}>
                <CommentContainer last={this.props.last}  repliesShowing={repliesShowing} child={this.props.child}>
                    <PathDebug>Path: {this.props.path.join("-")}</PathDebug>
                    <Header>
                        <ReplyTo mobile={true} dangerouslySetInnerHTML={{__html: reply_to}}></ReplyTo>
                        <Author>
                            <Avatar src={this.props.profile_image} />
                            <AuthorDetails>
                                <ReplyTo tablet={true} dangerouslySetInnerHTML={{__html: reply_to}}></ReplyTo>
                                <AuthorName>{this.props.firstname} {this.props.lastname}</AuthorName>
                                <Company>{organisationString}</Company>

                            </AuthorDetails>
                        </Author>
                        <Edits>
                            { !stopIndent &&
                            <CommentCountContainer>
                                <CommentCount handler={this.showReplies} data-test="comment_count">{this.props.reply_count}</CommentCount>
                            </CommentCountContainer>
                            }
                            <Times>
                                <EditTime>{publishedDate}</EditTime>
                                {edited && 
                                    <EditTime edited={true}>Edited {edited}</EditTime>
                                }
                            </Times>

                        </Edits>
                    </Header>
                    
                    { this.props.top_pick === 1 && !this.props.user.admin &&
                        <TopPick>Top comment</TopPick>
                    }

                    {this.props.user.admin && 
                        <Admin 
                            status          = {this.props.status}
                            topPick         = {this.props.top_pick}
                            editActive      = {this.state.enable_edit}
                            editHandler     = {this.enableEdit}
                            statusHandler   = {this.updateStatus} 
                            deleteHandler   = {this.toggleDeleteModal} 
                            topPickHandler  = {this.topPick}
                            pending         = {this.props.statusPending}
                        />
                    }

                    <Body admin={this.props.user.admin} toppick={this.props.top_pick} data-test="comment_body">{this.props.comment}</Body>
                    
                    {!this.state.enable_edit && this.props.status !== 'disapproved' &&
                    
                        <Footer>
                            {this.props.user.isLoggedIn &&  this.props.user.commenter && <>
                                <ReplyLink active={this.state.enable_reply} onClick={this.enableReply} data-test="comment_reply">Reply</ReplyLink>
                                <ReplyIcon onClick={this.enableReply} />
                            </> }
                            
                            {edited && !this.state.show_original &&
                                <EditPost onClick={this.showOriginal}>Show original</EditPost>
                            }

                            {edited && this.state.show_original &&
                                <EditPost onClick={this.hideOriginal}>Hide original</EditPost>
                            }

                            {!edited && this.props.user.isLoggedIn && this.props.user_id === this.props.user.details.user_id &&
                                <EditPost onClick={this.enableEdit}>Edit post</EditPost>
                            }


                            {show_reply_label &&
                                <ShowReplies onClick={this.showReplies} disabled={this.props.loading}>Show replies</ShowReplies>
                            }

                            {show_hide_reply_label &&
                                <ShowReplies onClick={this.hideReplies}>Hide replies</ShowReplies>
                            }
                        </Footer>
                    }
    
                    {this.state.enable_reply && 
                    <PostContainer>
                        <Message>Hi {this.props.user.details.firstname}, you are replying to {this.props.firstname} {this.props.lastname}</Message>
                        <CommentForm 
                            ref={this.formInput}
                            reply_to={this.props.id}
                            path={this.props.path}
                            placeHolder="Your comment"
                            submitHandler={this.props.postComment}
                            resetHandler={this.enableReply}
                            submitLabel= "Submit my post"
                            
                        />
                    </PostContainer>
                    }
                    
                    {this.state.show_original &&
                        <PostContainer>
                            <OriginalPostTitle>Original post</OriginalPostTitle>
                            {!this.props.previous && 
                                <RippleSpinner />
                            }
                            <OriginalPost>{this.props.previous}</OriginalPost>
                        </PostContainer>
                    }


                    {this.state.enable_edit && this.props.user.commenter && 
                    <PostContainer>
                        <CommentForm 
                            ref={this.formInput}
                            path={this.props.path}
                            value={this.props.comment}
                            submitHandler={this.editComment}
                            resetHandler={this.enableEdit}
                            submitLabel= "Submit changes"
                        />
                    </PostContainer>
                    }




                </CommentContainer>

                {this.props.children}


                {!flattening && hasLoadedReplies && this.props.reply_count > this.props.replies.length && repliesShowing &&
                    <LoadMore onClick={this.loadMore}>See more comments</LoadMore>
                }

                {flattening      && this.props.thread_count > this.props.replies.length && repliesShowing &&
                    <LoadMore onClick={() => this.loadMore(false)}>See more comments yo</LoadMore>
                }

                {this.props.loading && 
                // {true && 
                    <LoadPending>
                        <RippleSpinner />
                    </LoadPending>
                }




                {/* <Fade /> */}
            </OuterCommentContainer>
        )

    }
}

