const mapStateToProps = createStructuredSelector({
    user: userSelectors.selectUser,
    limit: selectors.selectLimit,
    threadLevel: selectors.selectThreadLevel,
    statusPending: selectors.selectStatusPending,
});


const mapDispatchToProps = dispatch => {
    return {
        topPick:        (id, path) => dispatch( commentCreators.topPick(id, path) ),
        postComment:    (comment) => dispatch( commentCreators.postComment(comment) ),
        showReplies:    (path) => dispatch({type: commentTypes.TOGGLE_SHOW_REPLIES, path}),
        getPrevious:    (id, path) => dispatch( commentCreators.getPrevious(id, path) ),
        editComment:    (id, comment) => dispatch( commentCreators.editComment(id, comment) ),
        updateStatus:   (id, path, status) => dispatch( commentCreators.updateStatus(id, path, status) ),
        fetchReplies:   (id, path, params) => dispatch(commentCreators.fetchComments(id, path, params)),
        toggleLoading:  (path) => dispatch({type: commentTypes.TOGGLE_LOADING, path}),
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(Comment);
