import * as selectors   from 'store/errors/selectors'

const SET_ERROR = "SET_ERROR";
const HIDE_ERROR = "HIDE_ERROR";

const setError = (error) => {
    return {
        type: SET_ERROR,
        error: error
    }
}
const hideError = () => {
    return {
        type: HIDE_ERROR
    }
}

const initState = {
    error: null,
    isOpen: false,
};

const errorReducer = (state = initState, action) => {
    const { error } = action;
    console.log(action);
    if (error) {
        return {
            error: error,
            isOpen: true
        }
    } else if (action.type === HIDE_ERROR) {
        return {
            error: null,
            isOpen: false
        }
    }

    return state;
}




const errorActions = {
    setError,
    hideError
};

const errorTypes = {
    SET_ERROR,
    HIDE_ERROR
}



export {
    errorTypes, 
    errorActions,
    selectors
};

export default errorReducer;