import React                            from 'react'
import { Formik, Form }                 from 'formik'
import * as Yup                         from 'yup'
import styled, {css}                    from 'styled-components'

// Components
import TextArea                         from 'components/Form/textArea'
import RippleSpinner                    from 'components/spinners/RippleSpinner'


const commentForm = React.forwardRef((props, ref) => {
    const { reply_to, path, submitHandler, resetHandler } = props;
    const initialValue = typeof props.value !== 'undefined' ? props.value : "";
    const submitLabel = typeof props.submitLabel !== 'undefined' ? props.submitLabel : "Submit my post";

    return (
        <Formik 
            initialValues={{
                comment: initialValue,
                reply_to: reply_to,
                path: path
            }} 
            validationSchema= {Yup.object({
                comment: Yup.string()
                    .required('Required'),
            })} 
            onSubmit={(values, { setSubmitting }) => {
                submitHandler(values).then(() => {
                    setSubmitting(false);
                    resetHandler();
                });
            }}
            onReset={() => {
                resetHandler();
            }}
        >



            {formik => (
                <>
                    <Form>
                        <InputContainer>

                            {formik.isSubmitting &&
                                <SpinnerContainer>
                                    <RippleSpinner />
                                </SpinnerContainer>
                            }

                            <TextArea 
                                onChange={formik.handleChange}
                                id="comment"
                                name="comment" 
                                placeholder={props.placeHolder}
                                disabled={props.disabled} 
                                value={formik.values.comment}
                                ref={ref}
                                style={{marginBottom:'14px'}}
                                data-test="commentform"
                                submitting={formik.isSubmitting}
                            />
                            {formik.errors.comment ? <div>{formik.errors.comment}</div> : null}
                            {formik.errors.article_id ? <div>{formik.errors.article_id}</div> : null}
                            {formik.errors.user_id ? <div>{formik.errors.user_id}</div> : null}
                        </InputContainer>
                    </Form>
                
                    <SubmitFooter>
                        <Faq>Questions? Check out our comments FAQ</Faq>
                        {!formik.isSubmitting &&
                            <Buttons>
                                <Button onClick={formik.handleReset} style={{marginRight:"7px"}}>Cancel</Button>
                                <Button red={true} onClick={formik.handleSubmit} data-test="submit">{submitLabel}</Button>
                            </Buttons>
                        }

                    </SubmitFooter>
                </>
            )}

        </Formik>    
    )
})

const InputContainer = styled.div`
    position:relative;
`

const SpinnerContainer = styled.div`
    position:absolute;
    left:50%;
    top:50%;
    transform: translate(-50%, -50%);
`

const Buttons = styled.div`
    display:flex;
    justify-content:flex-end;
    margin-left:auto;
`


const Button = styled.div`
    display:flex;
    font-size:15px;
    font-weight:600;
    text-transform:uppercase;
    color: black;
    cursor:pointer;
    ${props => props.red && css`
        color:#e22f22;
    `}
`

const SubmitFooter = styled.div` 
    display:flex;
    align-items:center;
`
const Faq = styled.p` 
    font-family: "Source sans pro";
    font-size:15px;
`
export default commentForm
