import axios from 'axios'
import Config from '../data/config'

const LOAD_CONFIG = 'config/LOAD_CONFIG'


const loadConfig = () => {

    return dispatch => {


        axios.get('/api/theme/get-config').then(r => {
            dispatch({
                type: LOAD_CONFIG,
                payload: r.data,
            });
        
        });
    

    }
}


const INITIAL_STATE = {
    config: Config
}

const configReducer = (state = INITIAL_STATE, action) => {

    switch (action.type) {  
        case LOAD_CONFIG:
            return {
                ...state,
                config: action.payload
            }
            default:
                return state;
    }
}


const configCreators = {
    loadConfig
};

const configTypes = {
    LOAD_CONFIG
};

export {
    configCreators,
    configTypes
};


export default configReducer