import React from 'react'
import Comment from './comment'
// import Comment from './test_comment'

export const CommentThread = (props) => {

    const { comments } = props;

    let style = props.child === true ? {display: 'none'} : null;
    if ( props.show) {
        // style = props.child === true ? {marginTop:'40px',paddingLeft: '30px'} : null;
        style = props.child === true ? {paddingLeft: '30px'} : null;
    } 

    // path stores the index path of the recursive structure
    // comment might exist at 0-1-1-2
    // third reply of the second reply of the second reply of the first comment
    // used to make updates to comments in redux store at the right place
    const path = typeof props.path !== 'undefined' ? props.path : [];

    return (
        <ul style={style}>
            { comments.map((comment, i) => {

                let renderReplies = false;
                if ( typeof comment.renderReplies !== 'undefined' && comment.renderReplies === true) {
                    renderReplies = true;
                } 

                comment.path = [...path];
                comment.path.push(i);
                const last = i === props.count -1;
                
                return (
                    <Comment 
                        key = {comment.id} 
                        last = {last} 
                        child = {props.child}
                        renderReplies = {renderReplies}
                        {...comment}
                        >
                        { comment.replies &&  
                            <CommentThread 
                                path = {comment.path}
                                comments = {comment.replies} 
                                child = {true} 
                                show = {renderReplies}
                                count = {comment.replies.length}
                            />}
                    </Comment>
                )
            })}
        </ul>    
    )
}


export default CommentThread;